.btn--main {
  background-color: var(--color-white);
  border: 0.1rem solid var(--color-light-grey);
  color: var(--color-black);
  cursor: pointer;
  display: inline-block;
  border-radius: 0.25rem;
  font-weight: 500;
  height: 3.8rem;
  line-height: 3.8rem;
  padding: 0 3rem;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.btn--main:focus, .btn--main:hover {
  background-color: var(--color-white);
  border-color: var(--color-black);
  color: var(--color-black);
  outline: 0;
}
.btn--main[disabled] {
  cursor: default;
  opacity: 0.5;
}
.btn--main[disabled]:focus, .btn--main[disabled]:hover {
  background-color: var(--color-black);
  border-color: var(--color-black);
}
.btn--main.button-outline {
  background-color: transparent;
  color: var(--color-black);
}
.btn--main.button-outline:focus, .btn--main.button-outline:hover {
  background-color: transparent;
  border-color: var(--color-dark-grey);
  color: var(--color-dark-grey);
}
.btn--main.button-outline[disabled]:focus, .btn--main.button-outline[disabled]:hover {
  border-color: inherit;
  color: var(--color-black);
}
.btn--main.button-clear {
  background-color: transparent;
  border-color: transparent;
  color: var(--color-black);
}
.btn--main.button-clear:focus, .btn--main.button-clear:hover {
  background-color: transparent;
  border-color: transparent;
  color: var(--color-dark-grey);
}
.btn--main.button-clear[disabled]:focus, .btn--main.button-clear[disabled]:hover {
  color: var(--color-black);
}

.btn--block, .wpcf7 input[type=submit] {
  background-color: var(--color-black);
  border: 0.1rem solid var(--color-black);
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
  cursor: pointer;
  display: inline-block;
  border-radius: 0.25rem;
  font-weight: 500;
  height: 3.8rem;
  line-height: 3.8rem;
  padding: 0 3rem;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.btn--block:focus, .wpcf7 input[type=submit]:focus, .btn--block:hover, .wpcf7 input[type=submit]:hover {
  background-color: var(--color-white);
  border-color: var(--color-black);
  color: var(--color-black);
  outline: 0;
}
.btn--block[disabled], .wpcf7 input[disabled][type=submit] {
  cursor: default;
  opacity: 0.5;
}
.btn--block[disabled]:focus, .wpcf7 input[disabled][type=submit]:focus, .btn--block[disabled]:hover, .wpcf7 input[disabled][type=submit]:hover {
  background-color: var(--color-white);
  border-color: var(--color-black);
  color: var(--color-black);
}

.btn--underline {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  cursor: pointer;
  background: transparent;
  border: none;
  border-radius: 0;
  width: auto;
  margin: 0;
  padding: 0;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  text-align: left;
  display: inline-block;
  padding: 0 3rem;
  border-bottom: 1px solid var(--color-black);
  text-align: center;
  line-height: 3.8rem;
  width: fit-content;
  background: transparent;
  color: var(--color-black);
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  font-weight: 500;
}
.btn--underline:hover {
  background: var(--color-black);
  color: var(--color-white);
}

.btn--gradient {
  align-items: center;
  background-image: linear-gradient(144deg, #af40ff, #5b42f3 50%, #00ddeb);
  border: 0;
  border-radius: 0.25rem;
  box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  color: var(--color-white);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  line-height: 1em;
  width: fit-content;
  padding: 0.125rem;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  cursor: pointer;
}

.btn--gradient:active,
.btn--gradient:hover {
  outline: 0;
}

.btn--gradient span {
  background-color: var(--color-black);
  padding: 16px 24px;
  border-radius: 0.25rem;
  width: 100%;
  height: 100%;
  transition: 300ms;
}

.btn--gradient:hover span {
  background: none;
}

.btn--plain {
  margin: 0;
}

.container {
  max-width: 90%;
